<template>
<div>
    <el-select v-model="value" placeholder="请选择" @change="changes">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value"

      >
    </el-option>
  </el-select>
  <el-form ref="form" label-width="80px" :model="list">
      <el-form-item label="特殊资源">
  <el-input-number :step='10' v-model="list.num" step-strictly @blur="numBlur" @change="changeNum" :max='20'>

  </el-input-number>
    </el-form-item>
  </el-form>
</div>

</template>

<script>
import Vue from 'vue';
  export default {
    name:"ceshi",
    data() {
      return {
        options: [{
          value: 1,
          label: '黄金糕'
        }, {
          value: 2,
          label: '双皮奶'
        }, {
          value: '选项3',
          label: '蚵仔煎'
        }, {
          value: '选项4',
          label: '龙须面'
        }, {
          value: '选项5',
          label: '北京烤鸭'
        }],
        value: '',
        list:{num:10}
      }
    },methods:{
      changes(){
        console.log(this.value);
      },numBlur(value){

        Vue.nextTick(()=>{
          // this.$set(this.list,'num',11)
            Vue.nextTick(()=>{
                        console.log(this.$refs.form.model.num);
            })

        })
      },changeNum(value){
          // console.log(value);
        //         this.$nextTick(()=>{
        //            console.log(this.$refs.form.model.num);
        // })
      }
    }
  }
</script>



